<template>
  <section class="step-2">
    <div class="add-place-des">
      <h4>Location Details</h4>
    </div>
    <ValidationObserver v-slot="{ invalid }">
      <form @submit.prevent="" class="form">
        <ValidationProvider
          name="houseNum"
          rules="required"
          v-slot="{ errors }"
        >
          <div class="form-input-wrapper">
            <label for="">House Num</label>
            <div class="form-input-element">
              <input v-model="houseNum" type="text" />
            </div>

            <span class="form-error">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>

        <ValidationProvider name="floor" rules="required" v-slot="{ errors }">
          <div class="form-input-wrapper">
            <label for="">Floor</label>
            <div class="form-input-element">
              <input v-model="floor" type="text" />
            </div>

            <span class="form-error">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>

        <ValidationProvider name="aptNum" rules="required" v-slot="{ errors }">
          <div class="form-input-wrapper">
            <label for="">Apartment Num</label>
            <div class="form-input-element">
              <input v-model="aptNum" type="text" />
            </div>
            
            <span class="form-error">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>

        <Navigation :valid="!invalid" />
      </form>
    </ValidationObserver>
  </section>
</template>

<script>
import Navigation from "./Navigation.vue";
import { ValidationObserver, ValidationProvider, extend } from "vee-validate";

extend("required", {
  validate(value) {
    return {
      required: true,
      valid: ["", " ", null, undefined].indexOf(value) === -1,
      message: "This field is required",
    };
  },
  computesRequired: true,
});

export default {
  components: { Navigation, ValidationObserver, ValidationProvider, extend },
  data() {
    return {};
  },
  computed: {
    houseNum: {
      get() {
        return this.$store.state.steps.step2.houseNum;
      },
      set(value) {
        this.$store.commit("setHouseNum", { houseNum: value });
      },
    },
    floor: {
      get() {
        return this.$store.state.steps.step2.floor;
      },
      set(value) {
        this.$store.commit("setFloor", { floor: value });
      },
    },
    aptNum: {
      get() {
        return this.$store.state.steps.step2.aptNum;
      },
      set(value) {
        this.$store.commit("setAptNum", { aptNum: value });
      },
    },
  },
  methods: {},
};
</script>

<style scoped lang="scss">
span {
  display: block;
}
</style>
